body {
  background-color: #282c34;
  color: #fff;
  font-family: 'Raleway', sans-serif;
}

header {
  margin-top: 20px;
  margin-bottom: 20px;
}

.tagline {
  text-align: center;
}

.my-name {
  font-weight: bold;
  font-size: 250%;
}

.my-motto {
  text-align: center;
}

.my-motto p {
  font-size: 100%;
  font-style: italic;
}

.my-credit {
  font-style: normal !important;
}

@media only screen and (max-width: 768px) {
  .my-name {
    font-size: 150%;
  }
  .my-motto p {
    font-size: 60%;
    margin-bottom: 4px;
  }
}