.tile {
  text-align: center;
}

.tile-icon {
  width: 100px;
  height: 100px;
}

.hidden {
  display: none;
}

@media only screen and (max-width: 768px) {
  .tile-icon {
    width: 50px;
    height: 50px;
  }
  .tile span {
    font-size: 80%;
  }
}

@media only screen and (max-width: 612px) {
  .tile span {
    font-size: 60%;
  }
}