.developer-wrapper {
  margin-top: 25px;
  background-color: #fff;
  border-radius: 10px;
}

.developer-wrapper h3, .developer-wrapper h5, .developer-wrapper span, .developer-wrapper p, .developer-wrapper li {
  color: #111;
}

.developer-heading {
  text-align: center;
}

.developer-heading h3 {
  margin-top: 10px;
}

.experience {
  overflow-y: scroll;
}

.experience h5 {
  font-weight: bold;
  margin-top: 20px;
}

.experience h5 a {
  color: inherit;
}

.experience span {
  font-style: italic;
}

.tech-icon-col {
  text-align: center;
}

.tech-icon {
  height: 70px;
  width: 70px;
}

@media only screen and (max-width: 768px) {
  .developer-heading h3 {
    font-size: 125%;
  }
  .experience h5 {
    font-size: 85%;
    margin-top: 0;
    margin-bottom: 0;
  }
  .experience h5 a {
    text-decoration: underline;
  }
  .experience span {
    font-size: 75%;
  }
  .experience ul {
    padding-left: 20px;
  }
  .experience li {
    font-size: 60%;
  }
  .tech-icon-text {
    font-size: 50%;
  }
  .tech-icon {
    height: 30px;
    width: 30px;
  }
}